
import React, { useState, useContext, useEffect } from "react";
import { ExpenseContext } from '../contexts/ExpenseContext'; 

import { Form, Button } from "react-bootstrap";

const ExpenseForm = ({ expenseToEdit, onClose }) => {
  const { addExpense, editExpense } = useContext(ExpenseContext);

  const [description, setDescription] = useState("");
  const [amount, setAmount] = useState("");
  const [date, setDate] = useState("");
  const [id, setId] = useState(null);

  useEffect(() => {
    if (expenseToEdit) {
      setDescription(expenseToEdit.description);
      setAmount(expenseToEdit.amount);
      setDate(expenseToEdit.date);
      setId(expenseToEdit.id);
    }
  }, [expenseToEdit]);

  const handleSubmit = (e) => {
    e.preventDefault();
    if (id) {
      editExpense({ id, description, amount, date });
    } else {
      addExpense({ id: Date.now(), description, amount, date });
    }
    onClose();
  };

  return (
    <Form onSubmit={handleSubmit}>
      <Form.Group controlId="description">
        <Form.Label>Description</Form.Label>
        <Form.Control
          type="text"
          placeholder="Enter description"
          value={description}
          onChange={(e) => setDescription(e.target.value)}
          required
        />
      </Form.Group>
      <Form.Group controlId="amount">
        <Form.Label>Amount</Form.Label>
        <Form.Control
          type="number"
          placeholder="Enter amount"
          value={amount}
          onChange={(e) => setAmount(e.target.value)}
          required
        />
      </Form.Group>
      <Form.Group controlId="date">
        <Form.Label>Date</Form.Label>
        <Form.Control
          type="date"
          value={date}
          onChange={(e) => setDate(e.target.value)}
          required
        />
      </Form.Group>
      <Button variant="primary" type="submit">
        {id ? "Update Expense" : "Add Expense"}
      </Button>
    </Form>
  );
};

export default ExpenseForm;
