// src/Avatar.js
import React from "react";

const Avatar = ({ user }) => {
  const defaultAvatar = "https://via.placeholder.com/150";

  return (
    <div>
      <img
        src={defaultAvatar}
        alt="User Avatar"
        style={{ width: "100px", height: "100px", borderRadius: "50%" }}
      />
      <p>{user.email}</p>
    </div>
  );
};

export default Avatar;
